(function($, options) {
    $(function() {

        const spPoint = options.breakpoints.sp;

        const getPosition = function(event){
            return {
                x: event.originalEvent.touches[0].pageX,
                y: event.originalEvent.touches[0].pageY
            };
        }

        let firstView = {};
        firstView.fv = $('#firstView');
        firstView.current = firstView.fv.find('.current__content .current__content__wrapper');
        firstView.more = firstView.fv.find('.more');
        firstView.li = firstView.more.find('li');
        firstView.moreLen = firstView.li.length;
        firstView.height = firstView.current.height();
        firstView.page = 1;
        firstView.position;
        firstView.direction;
        firstView.setPage = function(page) {
            firstView.fv.attr('data-page', firstView.page);
        }
        firstView.setPagerAction = function() {
            firstView.setPage();
            firstView.fv.on("touchstart", function(event){
                firstView.position = getPosition(event);
                firstView.direction = '';
            });
            firstView.fv.on("touchmove", function(event){
                const move = getPosition(event);
                if (firstView.position.x - move.x > 70) {
                    firstView.direction = 'left';
                } else if (firstView.position.x - move.x < -70){
                    firstView.direction = 'right';
                }
            });
            firstView.fv.on("touchend", function(){
                if(firstView.direction === 'left') {
                    if(firstView.page < firstView.moreLen) {
                        firstView.page++;
                    } else if(firstView.page === firstView.moreLen) {
                        firstView.page = 1;
                    }
                } else if(firstView.direction === 'right') {
                    if(firstView.page > 1) {
                        firstView.page--;
                    } else if(firstView.page === 1) {
                        firstView.page = firstView.moreLen;
                    }
                }
                if(options.windoWidth <= spPoint) {
                    firstView.setPage();
                }
            });
        }
        firstView.setMore = (()=>{
            let pagerLI = '';
            for(let i = 0; i < firstView.moreLen; i++) {
                const target = firstView.li.eq(i);
                pagerLI = pagerLI + `<li>${i + 1}</li>`;
            }
            const pager = $('<ul/>', {class: 'pager'}).append(pagerLI);
            firstView.fv.append(pager);
            firstView.setPagerAction();
        })();
        firstView.setHeight = function() {
            firstView.fv.height(firstView.current.height());
            firstView.fv.height(firstView.current.height());
            firstView.height = firstView.current.height();
            // firstView.li.height(firstView.height / 4);
        }
        // ファーストビューキーワード制御
        firstView.keyword = firstView.fv.find('.keyword');
        // ファーストビュー動画設置
        const tag = document.createElement('script');
        tag.src = "https://www.youtube.com/iframe_api";
        const firstScriptTag = document.getElementsByTagName('script')[0];
        firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
        function onPlayerStateChange(event) {
            console.log(firstView.player[0])
            console.log(firstView.player[0].getDuration())
            // firstView.player[0].getCurrentTime();
        }
        window.onYouTubeIframeAPIReady=function() {
            firstView.video = firstView.fv.find('.unit.video');
            firstView.videoLen = firstView.video.length;
            firstView.player = [];
            for(let i = 0; i < firstView.videoLen; i++) {
                const id = firstView.video.eq(i).attr('id');
                const videoid = firstView.video.eq(i).attr('data-videoid');
                firstView.player[i] = new YT.Player(id, {
                    height: '360',
                    width: '640',
                    videoId: videoid,
                    events: {
                        // 'onReady': onPlayerReady,
                        'onStateChange': onPlayerStateChange
                    }
                });
            }
            firstView.unit = firstView.fv.find('.unit');
            firstView.unit.eq(firstView.page - 1).addClass('show');
        }
        // ファーストビュー切り替え
        firstView.content = firstView.fv.find('.current__content');
        firstView.cover = `<div class="cover"><div class="keyword"><p><img src="/img/home/txt_ribon.png" alt=""></p></div></div>`;
        firstView.count;
        firstView.li.on('click', function() {
            const index = firstView.li.index(this);
            if(index + 1 != firstView.page) {
                firstView.page = index + 1;
                clearTimeout(firstView.count);
                firstView.content.find('.cover').remove();
                firstView.content.prepend(firstView.cover);
                firstView.count = setTimeout(() => {
                    firstView.unit.removeClass('show');
                    firstView.unit.eq(index).addClass('show');
                }, 2000);
            }
        });


        //カレンダー制御
        let calendar = {};
        calendar.target = $('#releaseCalendar');
        calendar.wrap = calendar.target.find('.content__wrapper');
        calendar.sec = calendar.target.find('section');
        calendar.prev = calendar.target.find('.btn__prev');
        calendar.next = calendar.target.find('.btn__next');
        calendar.month = 1;
        calendar.direction = 'right';
        calendar.moveMonth = calendar.target.find('.move__month');
        calendar.moveMonthLi = calendar.moveMonth.find('li');
        calendar.speed = 1;
        calendar.maxpage = calendar.sec.length;
        calendar.pacmanStart = false;
        calendar.pages = (()=>{
            let arr = [];
            for(let i = 0; i < calendar.maxpage; i++) {
                const _this = calendar.sec.eq(i);
                arr[i] = _this.find('li').length;
            }
            return arr;
        })();
        calendar.current = calendar.pages[0] + 1;
        calendar.child = (()=>{
            let child = 0;
            calendar.pages.forEach(element => {
                child = child + element;
            });
            return child;
        })();
        calendar.secPos = (()=>{
            let arr = [];
            for(let i = 0; i < calendar.maxpage; i++) {
                const _this = calendar.sec.eq(i);
                const pos = _this.position().left;
                arr[i] = pos;
            }
            return arr;
        })();


        calendar.pacman = {};
        calendar.pacman.set = false;
        calendar.pacman.obj = calendar.target.find('.pacman');
        calendar.pacman.nterval;
        calendar.pacman.distance = 128;
        calendar.pacman.target = 128;
        calendar.pacman.position = 128;
        calendar.pacman.speed = 2;
        calendar.pacman.direction = 'right';
        calendar.pacman.esa;
        calendar.pacman.esaPosition = [48, 80, 112, 176, 208, 240, 304, 336, 368];
        calendar.setPacman__esa = (()=> {
            let esa = '<span class="esa"><span></span><span></span><span></span></span>';
            for(var i = 0; i < calendar.moveMonthLi.length; i++) {
                if(i != calendar.moveMonthLi.length - 1) $('.move__month ul li').eq(i).append(esa);
            }
        })();
        calendar.pacman.eat = function(position) {
            const esa = $('.move__month .esa span');
            const pos = position + 14;
            const eat = (()=>{
                for(let i in calendar.pacman.esaPosition) {
                    let eat = calendar.pacman.esaPosition[i];
                    if(calendar.pacman.direction === 'right') {
                        if(pos >= eat && eat >= position) {
                            return i;
                            break;
                        }
                    } else {
                        if(eat >= pos && i < 3) {
                            return i;
                            break;
                        }
                    }
                }
                return false;
            })();
            if(eat) esa.eq(eat).addClass('eat');
        }
        calendar.pacman.eatCheck = function() {
            const esa = $('.move__month .esa span');
            const eat = $('.move__month .esa span.eat');
            const esaLen = esa.length;
            const eatLen = eat.length;
            const eatList = (()=>{
                let arr = [];
                for(let i = 0; i < esaLen; i++) {
                    let esaClass = (esa.eq(i).attr('class'))? esa.eq(i).attr('class') : '';
                    if(esaClass.match(/eat/)) {
                        arr.push(i);
                    }
                }
                return arr.reverse();
            })();
            eatList.forEach(function(elm){
                calendar.pacman.esaPosition.splice(elm, 1);
                eat.eq(elm).remove();
            });
        }
        calendar.pacman.move = function() {
            if(calendar.pacman.position === calendar.pacman.target) {
                calendar.pacman.position = calendar.pacman.target;
                calendar.moveMonth.attr('data-move', false);
                calendar.pacmanStart = false;
                calendar.pacman.obj.css({transform: 'translateX(' + calendar.pacman.target + 'px)'});
                clearInterval(calendar.pacman.nterval);
            } else {
                calendar.pacman.position = calendar.pacman.position + calendar.pacman.speed;
                calendar.pacman.obj.css({transform: 'translateX(' + calendar.pacman.position + 'px)'});
            }
            
            if(calendar.pacman.set) {
                if(calendar.pacman.esaPosition.length) calendar.pacman.eat(calendar.pacman.position);
            } else {
                calendar.pacman.set = true;
            }
        }

        calendar.setCurrent = function() {
            let page = 0;
            for(var i in calendar.pages) {
                const index = Number(i);
                page = page + calendar.pages[index];
                if(calendar.current <= page) {
                    if(calendar.month != index) calendar.moveMonth.attr('data-move', true);
                    calendar.pacman.target = calendar.pacman.distance * index;
                    calendar.month = index;
                    calendar.pacman.direction = (calendar.pacman.target < calendar.pacman.position)?  'left': 'right';
                    calendar.pacman.speed = (calendar.pacman.direction === 'right')? 4 : -4;
                    if(!calendar.pacmanStart) {
                        calendar.pacman.nterval = setInterval(calendar.pacman.move, 33);
                    }
                    calendar.pacmanStart = true;
                    break;
                }
            }
            calendar.moveMonth.attr('data-direction', calendar.pacman.direction);
            calendar.wrap.attr('data-current', calendar.current);
            calendar.moveMonthLi.removeClass('current');
            calendar.moveMonthLi.eq(calendar.month).addClass('current');
        }
        calendar.prev.on('click', function(){
            if(calendar.current > 1) {
                calendar.current--;
                calendar.setCurrent();
            }
        });
        calendar.next.on('click', function(){
            if(calendar.current < calendar.child) {
                calendar.current++;
                calendar.setCurrent();
            }
        });

        calendar.moveMonthLi.on('click', function(){
            const index = calendar.moveMonthLi.index(this);
            calendar.moveMonthLi.removeClass('current');
            $(this).addClass('current');
            if(index === 0) {
                calendar.current = 1;
            } else {
                const arr = calendar.pages.slice(0, index);
                const sum = (()=>{
                    let sum = 0;
                    arr.forEach(elm => {
                        sum = sum + elm;
                    });
                    return sum;
                })();
                calendar.current = sum + 1;
            }
            calendar.setCurrent();
        });



        let update = {};
        update.target = $('#update');
        update.group = update.target.find('.content__update >section');
        update.btn = update.target.find('.content__update nav ul li');
        update.btn.on('click', function(){
            const index = update.btn.index(this);
            update.btn.removeClass('current');
            $(this).addClass('current');
            update.group.removeClass('current');
            update.group.eq(index).addClass('current');
        });


        const news = {};
        news.target = $('.content__news');
        news.item = news.target.find('li');
        news.itemLarge = news.target.find('.large');

        // アニメーション位置の操作
        window.onload = function(){
            $('.animation-pos').scrollingAnimation({margin:300});
        }



        $(window).on('resize', function(){
            firstView.setHeight();
        });




        const init = (()=> {
            firstView.setHeight();
            calendar.setCurrent();
        })();

        let keywordNum = 0;
        $('#brandLogo').on('click', function(){
            if(keywordNum === 1) {
                $('.keyword').addClass('hide');
            } else if(keywordNum === 2) {
                $('.keyword').removeClass('hide');
                $('.keyword').removeClass('show');
                keywordNum = -1;
            } else {
                $('.keyword').addClass('show');
            }
            keywordNum++;
        });

    }); 
})(jQuery, options); 